@font-face {
  font-family: Megrim;
  src: url(../public/fonts/Megrim-Regular.ttf)
}

h3 {
  font-family: Megrim;
}

.App {
  width: 100%;
  height: 670px;
  display: flex;
  font-size: 12px;
  font-family: sans-serif;
  text-align: center;
  background:linear-gradient(#087ad8b9, transparent);
  background-color:rgb(66, 15, 71);
}

.Background {
  margin-left: 22%;
  margin-top: 3%;
  display: flex;
  font-size: 12px;
  font-family: sans-serif;
  text-align: center;
}

.Card {
  position: absolute;
  width: 200px;
  height: 515px;
  background-image: url("./img/bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 93%;
}


.card-container {
  position: relative;
  margin: 0 auto;
}

.global-mining-container {
  margin: auto;
  display: inline-block;
  vertical-align: middle;
  float: left;
  margin-left: 190px;
  padding: 2px;
}

.earth {
  border-radius: 500%;
  margin-top: -0.8px;
  margin-right: 5px;
  opacity: 93%;
  border: 1px solid rgb(203, 203, 204);
  cursor: auto !important;
}

/*global mining button*/

.global-mining-button {
  font-family: TitiReg;
  font-size: 15px;
  background-color:rgb(57, 1, 59);
  border: 2px solid rgba(251, 251, 255, 0.294);
  opacity: 85%;
}

.global-mining-button:hover {
  border: 2px solid #000000;
}

.alert {
  position: absolute;
  margin-top: -120px;
  margin-left: -14%;
  height: 150px;
  width: 500px;
  background:linear-gradient(rgb(32, 32, 32),transparent);
  border: 2px solid #201f1f;
  color: white;
  box-shadow: 0px -5px 30px rgb(37, 36, 36);
}

.close-button {
  margin-top: -5px;
  background-color: rgb(31, 2, 32);
  border: 1px solid #aaa7a7;
  color: #FFF;
}

.close-button:hover {
  color: white;
  background-color:rgb(52, 15, 54);
  border: 1px solid #aaa7a7;
  box-shadow: 0px 0px 10px rgb(110, 32, 114);
}

.CrystalImage {
  border-radius: 500%;
  border: 1px solid black;
  margin-top: 15px;
  margin-left: 10px;
  float: left;
  box-shadow: 0px -1px 10px rgb(146, 144, 144);
}

.CrystalImage:hover {
  border: 2px solid rgb(137, 137, 139);
  cursor: pointer;
}

.paragraph {
  font-size: 8px;
  border: 1px solid rgb(241, 235, 235);
  background:linear-gradient(rgba(141, 141, 146, 0.5),transparent);
  background-color:rgb(29, 23, 23);
  color: white;
  height: 200px;
  padding: 1%;
}

/* appear - on page load */
.fade-appear {
  opacity: 0;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity 1000ms linear;
}

/*on enter */
.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 1000ms linear;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity 1000ms linear;
}

.fade-exit-done {
  opacity: 0;
}

/* slide enter */
.slide-enter {
  opacity: 0;
  transform: scale(0.97) translateY(5px);
  z-index: 1;
}
.slide-enter.slide-enter-active {
  opacity: 1;
  transform: scale(1) translateY(0);
  transition: opacity 3000ms linear 1000ms, transform 3000ms ease-in-out 1000ms;
}

/* slide exit */
.slide-exit {
  opacity: 1;
  transform: scale(1) translateY(0);
}
.slide-exit.slide-exit-active {
  opacity: 0;
  transform: scale(0.97) translateY(5px);
  transition: opacity 1500ms linear, transform 1500ms ease-out;
}
.slide-exit-done {
  opacity: 0;
}

/*pagination customization*/

.pagination {
  margin-top: 222px;
  padding: 10px;
}

.page-items {
  padding: 1.4px;
}

.page-link {
  border-radius: 50%;
  width: 28px;
  height: 28px;
  opacity: 85%;
}

.page-link:active {
  opacity: 100%;
}


.page-link:hover {
  color: black;
  width: 31px;
  height: 31px;
}

/* table style */

.table {
  background-color: rgb(214, 214, 214);
  width: 38%;
  margin-left: 410px;
  box-shadow: 0px -5px 30px rgb(105, 99, 99);
}

td {
  text-align: left;
}

/* global mining alert transitions */

.alert-enter {
  opacity: 0;
  transform: scale(0.2);
}
.alert-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 200ms, transform 200ms;
}
.alert-exit {
  opacity: 1;
}

.alert-exit-active {
  opacity: 0;
  transform: scale(0.5);
  transition: opacity 200ms, transform 200ms;
}

.mining {
  font-family: TitiReg;
  font-size: 15px;
}

.alert-heading {
  font-family: Megrim;
  font-size: 25px;
}

/* rotate earth img */

.rotate{
  animation: rotation infinite 30s linear;
}

@keyframes rotation{
  from{
    transform:rotate(0deg);
  }

  to{
    transform:rotate(360deg);
  }
}

.title {
  font-family: Megrim;
  font-size: 40px;
  font-weight: bold;
}